import React from "react"
import Layout from "../components/layout"
import Banner from "../components/banner"
import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit'
import filterFactory, { textFilter, selectFilter } from 'react-bootstrap-table2-filter';
import { Link } from 'gatsby'
import "../styles/table.css"
import {useTranslation} from 'react-i18next';
import SEO from "../components/seo"
import LanguageIcon from '../../assets/menu_icon_languages.svg'

const { SearchBar } = Search

// Basic bible component
//import SEO from '../components/seo'

function languageLinkFormatter(cell, row) {
  return (<Link to={"/languages/" + row.iso }>{ cell }</Link>)
}

const LanguagesPage = ({ pageContext: { languages } }) => {

  const { t, i18n } = useTranslation()

  return (
    <Layout>
      <SEO title={ t('languages.title') } lang={ i18n.language } />
      <Banner
          title={ t('languages.title')}
          subtitle={ t('languages.description') }
          image='https://images.bible.cloud/fab/banners/languages_list.jpg'
          icon={LanguageIcon}
          breadcrumbs={[
            {path: '/', title: 'home'},
            {path: '/languages/', title: t('languages.title')}
          ]}
      />
      <div className="row">
        <div className="medium-12 columns centered">

          <ToolkitProvider
              keyField="id"
              data={languages}
              bordered={false}
              columns={[
                { dataField: 'name', text: t('fields.name'), sort: true, formatter: languageLinkFormatter },
                { dataField: 'iso', sort: true, text: 'iso', keyField: true, classes: 'text-center', headerClasses: 'text-center'},
                { dataField: 'bibles_count', sort: true, text: t('bibles.title'), classes: 'text-center', headerClasses: 'text-center'},
                { dataField: 'resources_count', sort: true, classes: 'col-small-hide text-center', headerClasses: 'col-small-hide  text-center', text: t('resources.title')}
              ]}
              search
          >
            {
              props => (
                  <div>
                    <SearchBar {...props.searchProps} delay={100}
                               placeholder={t('search.filterBy') + ' ' + languages.length + ' ' + t('languages.title')} />
                    <BootstrapTable
                        pagination={ paginationFactory({
                          sizePerPageList: [
                            {text: '50', value: 50},
                            {text: '100', value: 100},
                            {text: '250', value: 250}
                          ]
                        }) }
                        {...props.baseProps}
                    />
                  </div>
              )
            }
          </ToolkitProvider>


        </div>
      </div>
    </Layout>
  );
};

export default LanguagesPage
